.team__list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  max-width: 1200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 68px; }

.team__item {
  flex-basis: 240px;
  padding-top: 200px;
  position: relative; }
  .team__item::before {
    position: absolute;
    content: "";
    width: 133px;
    height: 133px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: #ef4358;
    border-radius: 20px; }
  .team__item::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-image: url(../img/team1.png);
    background-repeat: no-repeat;
    top: -30px;
    left: 40px; }
  .team__item:nth-of-type(2)::before {
    background-color: #70c217; }
  .team__item:nth-of-type(2)::after {
    background-image: url(../img/team2.png); }
  .team__item:nth-of-type(3)::before {
    background-color: #ffba49; }
  .team__item:nth-of-type(3)::after {
    background-image: url(../img/team3.png); }
  .team__item:nth-of-type(4)::after {
    background-image: url(../img/team4.png); }
  .team__item:nth-of-type(5)::before {
