.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 4px;
}

.nav__link {
  display: block;
  text-decoration: none;
  text-transform: capitalize;
  color: inherit;
  padding: 12px 20px;
  border-radius: 10px;

  &--accent {
    background-color: $red;

    &:hover {
      background-color: $red;
      box-shadow: 0 0 25px $red;
      transition: all 0.3s ease-in-out;
    }
  }
}
